import { style } from '../common/utils/style';
import { nextTick } from '../common/utils/system';
import { functionalMixin } from '../mixins/functional';
import { SwiperPureMixin } from '../mixins/pure/swiper';

import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';
import PressOverlay from '../press-overlay/press-overlay.vue';


const imagePreviewProps = {
  images: {
    type: Array,
    default: () => [],
  },
  startPosition: {
    type: [Number, String],
    default: 0,
  },
  transition: {
    type: String,
    default: 'press-fade',
  },
  showIndex: {
    type: Boolean,
    default: true,
  },
  asyncClose: {
    type: Boolean,
    default: false,
  },
  customStyle: {
    type: [String, Object],
    default: '',
  },
  customClass: {
    type: String,
    default: '',
  },
  closeable: {
    type: Boolean,
    default: false,
  },
  closeIcon: {
    type: String,
    default: 'clear',
  },
  closeIconPosition: {
    type: String,
    default: 'top-right',
  },
  zIndex: {
    type: Number,
    default: 10,
  },
};

export default {
  name: 'PressImagePreview',
  components: {
    PressOverlay,
    PressIconPlus,
  },
  mixins: [functionalMixin(imagePreviewProps), SwiperPureMixin],
  props: {
    ...imagePreviewProps,
  },
  emits: ['close', 'change'],
  data() {
    return {
      active: 0,
    };
  },
  computed: {
    imagePreviewStyle() {
      return style(this.getPropOrData('customStyle'));
    },
  },
  watch: {
    startPosition: 'setActive',
    show(val) {
      if (val) {
        this.setActive(+this.getPropOrData('startPosition'));
        nextTick(() => {
          // this.resize();
          // this.$refs.swipe.swipeTo(+this.startPosition, { immediate: true });
        });
      } else {
        this.$emit('close', {
          index: this.active,
          url: this.getPropOrData('images')[this.active],
        });
      }
    },
  },
  mounted() {
  },
  methods: {
    showImagePreview(options) {
      this.showDialog(options);
      this.setActive(options.startPosition || 0);
    },
    onClosed() {
    },
    onChange(event) {
      // swiper包装了event，可以用event.detail
      const { current = 0 } = event.detail;
      this.setActive(current);
    },
    emitClose() {
      if (!this.getPropOrData('asyncClose')) {
        this.$emit('close');
        this.innerShow = false;
        this.promiseCallback('confirm');
      } else {
        this.promiseCallback('cancel');
      }
    },
    onClose() {
      this.emitClose();
    },
    onClickOverlay() {
      this.onClose();
    },
    onClickSwiperItem() {
      this.onClose();
    },
    setActive(active) {
      if (active !== this.active) {
        this.active = active;
        this.$emit('change', active);
      }
    },
  },
};

